import { Injectable, UnauthorizedException } from '@nestjs/common';
import { PassportStrategy } from '@nestjs/passport';
import { Strategy, ExtractJwt } from 'passport-jwt';
import { User } from '../entities/user.entity';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

// PassportStrategy: NestJS„ÅßStrategy„Çí‰Ωø„ÅÑ„ÇÑ„Åô„Åè„Åô„Çãfunction
@Injectable()
export class JwtStrategy extends PassportStrategy(Strategy) {

    constructor(
        @InjectRepository(User)
        private userRepository: Repository<User>,
    ) {
        super({
            jwtFromRequest: ExtractJwt.fromAuthHeaderAsBearerToken(),
            ignoreExpiration: false,
            secretOrKey: 'secretKey123',
        });
    }

    // Ëá™Âãï„ÅßÂëº„Å≥Âá∫„Åó„Åï„Çå„ÇãÂá¶ÁêÜ„Åß„ÅÇ„Çä„ÄÅvalidate„ÅÆ„É°„ÇΩ„ÉÉ„ÉâÂêç„ÅØÂ§âÊõ¥‰∏çÂèØ
    async validate(payload: { id: string; username: string; }): Promise<User> {
        const { id, username } = payload;
        const user = await this.userRepository.findOneBy({ id, username });

        if (user) {
            return user;
        }
        throw new UnauthorizedException();
    }
}