import { Column, Entity, ManyToOne, PrimaryGeneratedColumn } from 'typeorm';
import { ItemStatus } from '../items/item-status.enum';
import { User } from './user.entity';

@Entity()
export class Item {
    @PrimaryGeneratedColumn('uuid')
    id: string;

    @Column()
    name: string;

    @Column()
    price: number;

    @Column()
    description: string;

    @Column({
        type: 'enum',
        enum: ItemStatus
    })
    status: ItemStatus;

    @Column()
    createdAt: string;

    @Column()
    updatedAt: string;

    @Column()
    userId: string;

    @ManyToOne(() => User, (user) => user.items)
    user: User;
}