import { Injectable, UnauthorizedException } from '@nestjs/common';
import { CreateUserDto } from './dto/create-user-dto';
import { CredentialsDto } from './dto/credentials.dto';
import { User } from '../entities/user.entity';
import { JwtService } from '@nestjs/jwt';

import * as bcrypt from 'bcrypt';
import { InjectRepository } from '@nestjs/typeorm';
import { Repository } from 'typeorm';

@Injectable()
export class AuthService {

    constructor(
        @InjectRepository(User) private readonly userRepository: Repository<User>,
        private readonly jwtService: JwtService,
    ) { }

    async signUp(createUserDto: CreateUserDto): Promise<User> {
        const { username, password, status } = createUserDto;
        const salt = await bcrypt.genSalt();
        const hashPassword = await bcrypt.hash(password, salt);

        const user = this.userRepository.create({
            username,
            password: hashPassword,
            status,
        });

        await this.userRepository.save(user);
        return user;
    }


    async signIn(
        credentialsDto: CredentialsDto,
    ): Promise<{ accessToken: string; }> {
        const { username, password } = credentialsDto;
        const user = await this.userRepository.findOneBy({ username });

        // compare(password, user.password) „Éë„Çπ„ÉØ„Éº„Éâ„ÅåÂêå„Åò„ÅãÊØîËºÉ
        // => password: ÂÖ•Âäõ„Åï„Çå„Åü„Éë„Çπ„ÉØ„Éº„Éâ vs user.password: DB„Å´‰øùÂ≠ò„Åó„Å¶„ÅÑ„ÇãHashÂåñ„Åï„Çå„Åüpassword
        if (user && (await bcrypt.compare(password, user.password))) {
            const payload = { id: user.id, username: user.username };
            const accessToken = this.jwtService.sign(payload);
            return { accessToken };
        }
        throw new UnauthorizedException(
            '„É¶„Éº„Ç∂„ÉºÂêç„Åæ„Åü„ÅØ„Éë„Çπ„ÉØ„Éº„Éâ„ÇíÁ¢∫Ë™ç„Åó„Å¶„Åè„Å†„Åï„ÅÑ',
        );
    }
}
