import { CanActivate, ExecutionContext, Injectable } from '@nestjs/common';
import { Reflector } from '@nestjs/core';

@Injectable()
// CanActivate: Guard„Å®„Åó„Å¶Ê©üËÉΩ„Åï„Åõ„Çã„Åü„ÇÅ„Å´ÂøÖË¶Å ‚ÄªË£úË∂≥: AuthGuard„ÅØCanActivate„ÇíÁ∂ôÊâø„Åó„Å¶„ÅÑ„Çã„Åü„ÇÅ‰∏çË¶Å
export class RolesGuard implements CanActivate {

    /**
     *
     */
    constructor(private reflector: Reflector) { // Reflector: „Éá„Ç≥„É¨„Éº„Çø„Éº„Åß„Çª„ÉÉ„Éà„Åó„Åü„É°„Çø„Éá„Éº„Çø„ÇíÂèñÂæó
    }

    canActivate(ctx: ExecutionContext): boolean {
        const requiredStatuses = this.reflector.get<string[]>(
            'statuses',
            ctx.getHandler(),
        );

        // „Éá„Ç≥„É¨„Éº„Çø„Éº„Å´‰Ωï„ÇÇÊåáÂÆö„Åï„Çå„Å¶„ÅÑ„Å™„ÅÑÂ†¥Âêà„ÅØÂÆüË°å„ÇíË®±ÂèØ
        if (!requiredStatuses) {
            return true;
        }

        // „É¶„Éº„Ç∂„Éº„ÅÆstatus„Åå„ÄÅ„É°„Çø„Éá„Éº„Çø„Åã„ÇâÂèñÂæó„Åó„Åüstatus„ÅÆ„ÅÑ„Åö„Çå„Åã„Å´‰∏ÄËá¥„Åô„Çå„Å∞ÂÆüË°å„ÇíË®±ÂèØ
        const { user } = ctx.switchToHttp().getRequest();
        return requiredStatuses.some((status) => user.status.includes(status));
    }
}